import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { SubconfigComponent } from '../subconfig/subconfig.component';
import { ToasterService } from 'angular2-toaster';
import { SubscribeService } from '../service/subscribe/subscribe.service';

@Component({
  selector: 'app-subscribe',
  templateUrl: './subscribe.component.html',
  styleUrls: ['./subscribe.component.css']
})
export class SubscribeComponent implements OnInit {
  disable: boolean // 

  data: any // 
  @Input()
  set value(data: any) {
    if (!data) {
      return
    }
    this.data = data
  }
  @Output('clickEvt')
  click = new EventEmitter<any>() // 

  constructor(
    private subService: SubscribeService,
    private toaster: ToasterService,
    private dialog: MatDialog,
  ) {
  }

  ngOnInit(): void {

  }

  // 
  openSubconfigWindow(v: any) {
    this.dialog.open(SubconfigComponent, {
      width: "45%",
      data: {
        "op": "update",
        "value": v,
      }
    })
  }
  // 
  remove(data: any) {
    this.disable = true

    this.subService.delete<any>({
      ID: data.ID,
    }).then(() => {
      this.toaster.pop("success", "")
      this.click.emit(this.data)
    }).catch(() => {
      this.toaster.pop("error", "")
    }).finally(() => {
      this.disable = false
    })
  }

}
